@mixin dark-mode {
    $text-color: $white;
    $timeline-item-font-color: $text-color;
    $timeline-background-color: #2e2e2e;
    $timeline-color: $medium-gray;

    html, body { background-color: #121212; }
    h1, h2, h3, h4, p, ul , li { color: $text-color; }
    .page-wrapper > .row > .col:last-child { background-color: $timeline-background-color; }
    .info {
        &-row { color: $text-color;
            .icon { color: $radiant-blue; }
        }
        .address, .birthday-date, a { color: $text-color; }
    }
    .timeline {
        &-dot { border-color: $timeline-background-color; }
        &-content {
            p, a, strong, em {
                color: $text-color;
            }
        }
    }
}

@media (prefers-color-scheme: dark) { @include dark-mode; }
@media not print {
    .dark-mode { @include dark-mode; }
}